<template>
  <div class="bheader">
    <span class="bheader__iconMenu" v-show="!showMenuIcon" @click='showLeftSide'><i class="el-icon-menu"></i></span>
    <div class="bheader__profile">
      <img src="../assets/logo.png" alt="" class="profile__avatar">
      <el-dropdown>
        <span class="el-dropdown-link">
          {{currentUser.username}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  props:['showMenuIcon'],
  computed: {
    ...mapState(["currentUser"])
  },
  methods:{
    showLeftSide(){
      this.$emit('showLeftSide');
    }
  }
};
</script>
<style lang="scss" scoped>
.bheader {
  background-color: rgb(206, 166, 166);
  height: 100%;
  position: relative;

  .bheader__profile{
    position: absolute;
    right: 5px;
    display: flex;
    align-items: center;
    transform: translateY(-50%);
    top: 50%;
    .profile__avatar{
      width: 40px;
      height: 40px;
    }
    .el-dropdown{
      cursor: pointer;
      font-size: 16px;
    }
  }

  .bheader__iconMenu{
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    cursor: pointer;

    &:hover{
      color: rgb(216, 216, 199);
    }
  }
}
</style>



